import React, { useEffect } from "react";
import Cookies from "js-cookie";
import "../styles/Modal.css";

const Modal = ({ onClose }) => {
  useEffect(() => {
    const hasAcceptedCookies = Cookies.get("acceptedCookies");
    if (!hasAcceptedCookies) {
      // Mostrar el modal si las cookies no han sido aceptadas
      document.querySelector(".modal").style.display = "block";
    }
  }, []);

  const handleAcceptCookies = () => {
    // Establecer la cookie y cerrar el modal
    Cookies.set("acceptedCookies", "true", { expires: 365 }); // La cookie expirar en 1 ao
    onClose();
  };

  return (
    <div className="modal">
      <div className="modal-content">
        <span className="close" onClick={onClose}>
          &times;
        </span>
        <h2>Poltica de Cookies</h2>
        <p>
          Esta pgina web hace uso de cookies,
          al hacer click en "aceptar" aceptas cualquier
          cookie de este sitio web
        </p>
        <button onClick={handleAcceptCookies}>Aceptar</button>
      </div>
    </div>
  );
};

export default Modal;
